import './Header.css';

function Header(){
return(

    <nav className='navbar   navbar-expand-md    header '  >


    <div className='container'>
     <a href='altafraja.com' className='navbar-brand logo'>Altaf Raja</a>  
       <button className='navbar-toggler' data-toggle='collapse' data-target='#mynav'>
        <span className='navbar-toggler-icon'></span>
       </button>

        <div className='collapse  header-link navbar-collapse' id='mynav'>
       <ul className='navbar-nav ml-auto text-center'>
       <li className='nav-item'><a className='nav-link ' href='#'>About</a></li>
       <li className='nav-item'><a className='nav-link' href='#'>Skills</a></li>
       <li className='nav-item'><a className='nav-link' href='#'>Projects</a></li>
       <li className='nav-item'><a className='nav-link' href='#'>Blogs</a></li>
       <li className='nav-item  connect '><a className='nav-link' href='#'>Connect</a></li>
       </ul>

       </div>


       </div>




    </nav>
)

}

export default Header;